import { JDGetNewsInfo } from '@/api/getData'
export default {
  data() {
    return {
        Id: this.$route.query.id,
        info: ''
    };
  },
  components: {
  },
  mounted() {
    this.getInfo()
  },
  methods: {
    async getInfo() {
      let res = await JDGetNewsInfo({Id: this.Id})
      this.info = res.obj
      console.log(res)
    }
  }
};



